body {
    font-family: "Raleway", sans-serif;
    background-color: #100e1d;
    margin: 0;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
    cursor: pointer;
}

.sidebar {
    background-color: #1e213a;
    text-align: center;
    padding-top: 2rem;
}

.weather-temp {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

h4 {
    font-size: 18px;
    color: #e7e7eb;
}

h3 {
    font-size: 30px;
    color: #e7e7eb;
}

.weather-temp li {
    font-size: 18px;
}

.max-temp {
    color: #e7e7eb;
}

.min-temp {
    color: #a09fb1;
}

.open-modal {
    font-size: 16px;
    color: #e7e7eb;
    background-color: #88869d;
    padding: 18px 16px;
    border: none;
    cursor: pointer;
}

.temperature-btn {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.btn {
    font-size: 18px;
    color: #e7e7eb;
    background-color: #1e213a;
    border: none;
    padding: 22px;
    border-radius: 50%;
    margin-right: 10px;
    cursor: pointer;
}

.btn:focus {
    outline: none;
}

.active {
    background-color: #e7e7eb;
    color: #1e213a;
}

.weather-card {
    padding: 1rem;
}

.weather-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;
}

.weather-list-item {
    background-color: #1e213a;
    padding: 1rem;
    text-align: center;
    margin-top: 2rem;
}

.weather-today {
    padding: 1rem;
}

.icon {
    width: 24px;
}

.location {
    display: flex;
    justify-content: center;
}

.temperature {
    font-size: 6rem;
    line-height: 1rem;
    color: white;
}

.weather-name {
    font-size: 2.25rem;
    line-height: 2.5rem;
    color: darkgrey;
}

.location p,
.date {
    color: darkgrey;
    font-size: 18px;
}

.weather-today .weather-icon {
    width: 170px;
}

.highlights {
    text-align: center;
    color: #e7e7eb;
}

.highlights-details {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
}

.prediction-today {
    background-color: #1e213a;
    padding: 1rem;
    margin-top: 1rem;
}

.prediction-today p {
    font-size: 30px;
}

.number {
    font-weight: bold;
}

.length {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

progress {
    width: 100%;
}

.percentage {
    display: flex;
    justify-content: flex-end;
}

.form-input {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #1e213a;
    width: 100%;
    height: 1500vh;
    padding: 1rem;
}

.form {
    display: flex;
    justify-content: space-evenly;
}

input {
    padding: 1rem;
    background: transparent;
    border: 1px solid gray;
    color: #e7e7eb;
}

.button {
    background-color: #3182ce;
    font-size: 16px;
    color: white;
    padding: 1rem;
    border: none;
}

.city-names {
    display: flex;
    flex-direction: column;
    margin-top: 70px;
}

.cities {
    padding: 1rem;
    background-color: transparent;
    color: white;
    border: none;
    text-align: initial;
    font-size: 18px;
    cursor: pointer;
}

.footer {
    color: #616475;
    font-size: 16px;
}

.footer p {
    text-align: center;
    margin: 0;
    padding: 1rem;
}

.loading {
    color: white;
    font-size: 18px;
}

@media (min-width: 1114px) {
    .sidebar {
        height: 100%;
        width: 400px;
        position: fixed;
        z-index: 1;
        top: 0;
        left: 0;
        overflow-x: hidden;
        padding-top: 20px;
        text-align: center;
    }
    .form-input {
        position: absolute;
        top: 0;
        left: 0;
        background-color: #1e213a;
        width: 400px;
        height: 100%;
        padding: 1rem;
    }
    .weather-card {
        padding-left: 500px;
    }
    .weather-list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .highlights-details {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        column-gap: 20px;
        row-gap: 20px;
    }
    .footer {
        padding-left: 600px;
    }
    .footer p {
        display: flex;
        flex-direction: row;
    }
}